<template>
    <div id="new-employee">
        <h3>New Employee</h3>
        <div class="row">
            <form @submit.prevent="saveEmployee()" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input id="input1" type="text" v-model="employee_id" required>
                        <label for="input1">Employee ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input id="input2" type="text" v-model="name" required>
                        <label for="input2">Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input id="input3" type="text" v-model="dept" required>
                        <label for="input3">Department</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input id="input4" type="text" v-model="position" required>
                        <label for="input4">Position</label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit';
    export default {
        name: 'new-employee',
        data() {
            return {
                employee_id: null,
                name: null,
                dept: null,
                position: null
            }
        },
        methods: {
            saveEmployee() {
                db.collection('emplyees').add({
                   employee_id: this.employee_id,
                   name: this.name,
                   dept: this.dept,
                   position: this.position
                })
                .then(docRef => this.$router.push('/'))
                .catch(err => console.log(err))
            }
        }
    }
</script>



